html {
  font-size: 62.5%;
  height: 100%;
}

@media screen and (min-width: 1780px) and (max-width: 1919px) {
  html {
    font-size: 58%;
  }
}

@media screen and (min-width: 1680px) and (max-width: 1779px) {
  html {
    font-size: 55%;
  }
}

@media screen and (min-width: 1580px) and (max-width: 1679px) {
  html {
    font-size: 52%;
  }
}

@media screen and (min-width: 1480px) and (max-width: 1579px) {
  html {
    font-size: 50%;
  }
}

@media screen and (max-width: 1479px) {
  html {
    font-size: 48%;
  }
}

body {
  font-family: 'Archer-Bold';
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #fff;
  font-size: 14px;
  font-size: 1.4rem;
  overflow-x: hidden;
}

h1 {
  font-size: 83px;
  font-size: 8.3rem;
  margin: 0;
}

@media screen and (max-width: 1080px) {
  h1 {
    font-size: 50px;
  }
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 40px;
  }
}

h2 {
  font-size: 80px;
  font-size: 8rem;
  letter-spacing: 3px;
}

@media screen and (max-width: 1080px) {
  h2 {
    font-size: 50px;
  }
}

@media screen and (max-width: 768px) {
  h2 {
    font-size: 40px;
  }
}

h3 {
  font-size: 50px;
  font-size: 5rem;
}

@media screen and (max-width: 768px) {
  h3 {
    font-size: 22px;
  }
}

h4 {
  font-size: 23px;
  font-size: 2.3rem;
}

@media screen and (max-width: 768px) {
  h4 {
    font-size: 14px;
  }
}

h5 {
  font-size: 19px;
  font-size: 1.9rem;
}

h6 {
  font-size: 13px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: Archer-Bold;
  line-height: 1;
  color: #fff;
}

p, li {
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
  font-family: Archer-Bold;
}

@media screen and (max-width: 1440px) {
  p, li {
    font-size: 14px;
  }
}

@media screen and (max-width: 1380px) {
  p, li {
    font-size: 13px;
  }
}

/* =========================

        GENERAL STYLES

   =========================*/
*, *:before, *:after {
  box-sizing: border-box;
  border: none;
}

a:link,
a:visited,
a:active {
  color: #fff;
  pointer-events: auto;
  text-decoration: none;
}

a:link:hover,
a:visited:hover,
a:active:hover {
  color: #dbdbdb;
  transition: color 0.3s;
}

/**************************************************/
/*               UTILITY CLASSES                 ;*/
/**************************************************/
.content-inside {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-size: cover;
  background-position: center center;
  /*    background-color: #fafafa;*/
  background-repeat: no-repeat;
  width: 100%;
  overflow: hidden;
}

.display-none {
  display: none;
}

.clear-padding {
  padding: 0;
}

.center {
  text-align: center;
}

.no-padding {
  padding-right: 0;
  padding-left: 0;
}

.no-padding-right {
  padding-right: 0;
}

.no-margin {
  margin-left: 0;
  margin-right: 0;
}

.overflow-hide {
  overflow: hidden;
}

.overflowx-hide {
  overflow-x: hidden;
}

.overflowy-hide {
  overflow-y: hidden;
}

.error-message {
  color: red !important;
  display: none;
  font-style: italic;
  font-size: 11px;
  position: absolute;
  bottom: 0;
  right: 0;
}

/**************************************************/
/*               UTILITY CLASSES END             ;*/
/**************************************************/
/**************************************************/
/*                ANIMATION CLASSES              ;*/
/**************************************************/
[data-animate] {
  opacity: 0;
}

.no-csstransitions [data-animate], .no-cssanimations [data-animate] {
  opacity: 1;
}

.revealOnScroll {
  opacity: 0;
}

.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-timing-function: ease;
  animation-timing-function: ease;
}

.animated.infinite {
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.animate-translate-out {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.translateOutUp {
  -webkit-animation-name: translateOutUp;
  animation-name: translateOutUp;
}

@-webkit-keyframes translateOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}

@keyframes translateOutUp {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    -webkit-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
}

.translateOutDown {
  -webkit-animation-name: translateOutDown;
  animation-name: translateOutDown;
}

@-webkit-keyframes translateOutDown {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
}

@keyframes translateOutDown {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
  }
}

.translateOutLeft {
  -webkit-animation-name: translateOutLeft;
  animation-name: translateOutLeft;
}

@-webkit-keyframes translateOutLeft {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
}

@keyframes translateOutLeft {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
  }
}

.translateOutRight {
  -webkit-animation-name: translateOutRight;
  animation-name: translateOutRight;
}

@-webkit-keyframes translateOutRight {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes translateOutRight {
  from {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  to {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }
}

.translateOutLeft-poster {
  -webkit-animation-name: translateOutLeft-poster;
  animation-name: translateOutLeft-poster;
}

@-webkit-keyframes translateOutLeft-poster {
  from {
    box-shadow: 0px 0px 0px 0px #0a0a0a;
    opacity: 1;
  }
  to {
    opacity: 1;
    box-shadow: 1px 1px 30px 9px #0a0a0a;
  }
}

@keyframes translateOutLeft-poster {
  from {
    opacity: 1;
    box-shadow: 0px 0px 0px 0px #0a0a0a;
  }
  to {
    opacity: 1;
    box-shadow: 1px 1px 30px 9px #0a0a0a;
  }
}

.fadeOut-poster {
  -webkit-animation-name: fadeOut-poster;
  animation-name: fadeOut-poster;
}

@-webkit-keyframes fadeOut-poster {
  from {
    width: auto;
  }
  to {
    width: 0;
  }
}

@keyframes fadeOut-poster {
  from {
    width: 100%;
  }
  to {
    width: 0;
  }
}

@-webkit-keyframes fadeDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -20px, 0);
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -20px, 0);
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.fadeDown {
  -webkit-animation-name: fadeDown;
  animation-name: fadeDown;
}

@-webkit-keyframes fadeUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -20px, 0);
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 30px, 0);
    transform: translate3d(0, 30px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.fadeUp {
  -webkit-animation-name: fadeUp;
  animation-name: fadeUp;
  -webkit-animation-duration: 0.6s;
  animation-duration: 0.6s;
}

@-webkit-keyframes fadeRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(30px, 0, 0);
    transform: translate3d(30px, 0, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(30px, 0, 0);
    transform: translate3d(30px, 0, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.fadeRight {
  -webkit-animation-name: fadeRight;
  animation-name: fadeRight;
}

@-webkit-keyframes fadeLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-30px, 0, 0);
    transform: translate3d(-30px, 0, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

@keyframes fadeLeft {
  from {
    opacity: 0;
    -webkit-transform: translate3d(-30px, 0, 0);
    transform: translate3d(-30px, 0, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.fadeLeft {
  -webkit-animation-name: fadeLeft;
  animation-name: fadeLeft;
}

@-webkit-keyframes scale {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  75% {
    opacity: 1;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes scale {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  75% {
    opacity: 1;
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.scale {
  -webkit-animation-name: scale;
  animation-name: scale;
  -webkit-animation-duration: 0.6s;
  animation-duration: 0.6s;
}

@-webkit-keyframes opacityAnimate {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes opacityAnimate {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.opacityAnimate {
  -webkit-animation-name: opacityAnimate;
  animation-name: opacityAnimate;
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
}

/**************************************************/
/*             ANIMATION CLASSES END             ;*/
/**************************************************/
.asdasdasd {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-transform: translate(50%, 50%);
  -moz-transform: translate(50%, 50%);
  -ms-transform: translate(50%, 50%);
  -o-transform: translate(50%, 50%);
  transform: translate(50%, 50%);
  position: relative;
}

.asdasdasd .test {
  text-decoration: none;
}
