//Variable
//$test : #fff;

$mobile: 767px;
$tablet: 768px;
$tablet-max: 1080px;
$laptop: 1380px;
$medium-laptop: 1440px;
$medium-desktop: 1680px;
$large-desktop: 1980px;

//Heading Font style
$h-font-family: Archer-Bold;

//p,li font style
$p-li-color:#fff;
$p-li-font-size:16px;
$p-li-font-family: Archer-Bold;

@mixin media-query-min($width) {
    @media screen and (min-width:$width) {
        @content;
    }
}

@mixin media-query-max($width) {
    @media screen and (max-width:$width) {
        @content;
    }
}

@mixin media-query-mobile {
    @media screen and (max-width:$mobile) {
        @content;
    }
}

@mixin media-query-tablet-portrait {
    @media screen and (max-width:$tablet) and (orientation: portrait) {
        @content;
    }
}

@mixin media-query-tablet-max {
    @media screen and (max-width:$tablet-max) {
        @content;
    }
}

@mixin media-query-laptop {
    @media screen and (max-width:$laptop) {
        @content;
    }
}

@mixin media-query-medium-laptop {
    @media screen and (max-width:$medium-laptop) {
        @content;
    }
}

@mixin media-query-medium-desktop {
    @media screen and (max-width:$medium-desktop) {
        @content;
    }
}

@mixin media-query-large-desktop {
    @media screen and (max-width:$large-desktop) {
        @content;
    }
}

@mixin tranlsate($x,$y){
    -webkit-transform: translate($x,$y);
       -moz-transform: translate($x,$y);
        -ms-transform: translate($x,$y);
         -o-transform: translate($x,$y);
            transform: translate($x,$y);
}

@mixin transition($style, $time){
    -webkit-transition: $style $time ease;
       -moz-transition: $style $time ease;
        -ms-transition: $style $time ease;
         -o-transition: $style $time ease;
            transition: $style $time ease;
}

@mixin img-bg($size){
    background-size: $size; 
    background-position: center center; 
    background-repeat: no-repeat;
}


html{
    font-size: 62.5%;
    height: 100%;

    @media screen and (min-width:1780px) and (max-width:1919px){
        font-size: 58%;
    }

    @media screen and (min-width:1680px) and (max-width:1779px){
        font-size: 55%;
    }

    @media screen and (min-width:1580px) and (max-width:1679px){
        font-size: 52%;
    }

    @media screen and (min-width:1480px) and (max-width:1579px){
        font-size: 50%;
    }

    @media screen and (max-width:1479px){
        font-size: 48%;
    }
}

body{
    font-family: 'Archer-Bold';
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #fff;
    font-size: 14px;
    font-size: 1.4rem;
    overflow-x: hidden;
   // &.modal-open {
   //     overflow: hidden;
   //     position: fixed;
   // }
}

h1{
    font-size: 83px;
    font-size: 8.3rem;
    margin: 0;

    @media screen and (max-width:1080px) {
        font-size: 50px;
    }

    @include media-query-max(768px) {
        font-size: 40px;
    }
}

h2{
    font-size: 80px;
    font-size: 8rem;
    letter-spacing: 3px;

    @media screen and (max-width:1080px) {
        font-size: 50px;
    }

    @include media-query-max(768px) {
        font-size: 40px;
    }
}

h3{
    font-size: 50px;
    font-size: 5rem;

    @include media-query-max(768px) {
        font-size: 22px;
    }
}

h4{
    font-size: 23px;
    font-size: 2.3rem;

    @include media-query-max(768px) {
        font-size: 14px;
    }
}

h5{
    font-size: 19px;
    font-size: 1.9rem;
}

h6 {
    font-size: 13px;
}

h1,h2,h3,h4,h5,h6{
    font-family: $h-font-family;
    line-height: 1;
    color: #fff;
}

p, li{
    font-size: $p-li-font-size;
    line-height: 1.5;
    color: $p-li-color;
    font-family: $p-li-font-family;

    @media screen and (max-width:1440px) {
        font-size: #{$p-li-font-size - 2px};
    }

    @media screen and (max-width:1380px) {
        font-size: #{$p-li-font-size - 3px};
    }
}

  /* =========================

        GENERAL STYLES

   =========================*/
*, *:before, *:after {
  box-sizing: border-box;
  border: none; }
a:link,
a:visited,
a:active {
    color: #fff;
    pointer-events: auto;
    text-decoration: none;
}
a:link:hover,
a:visited:hover,
a:active:hover {
    color: #dbdbdb;
    transition: color 0.3s;
}


/**************************************************/
/*               UTILITY CLASSES                 ;*/
/**************************************************/

.content-inside{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-size: cover;
    background-position: center center;
/*    background-color: #fafafa;*/
    background-repeat: no-repeat;
    width: 100%;
    overflow: hidden;
}

.display-none{
    display: none;
}

.clear-padding{
    padding: 0;
}

.center{
    text-align: center;
}

.no-padding{
    padding-right: 0;
    padding-left: 0;
}

.no-padding-right{
    padding-right: 0;
}

.no-margin{
    margin-left: 0;
    margin-right: 0;
}

.overflow-hide{
    overflow: hidden;
}

.overflowx-hide{
    overflow-x: hidden;
}

.overflowy-hide{
    overflow-y: hidden;
}

.error-message {
    color: red !important;
    display: none;
    font-style: italic;
    font-size: 11px;
    position: absolute;
    bottom: 0;
    right: 0;
}


/**************************************************/
/*               UTILITY CLASSES END             ;*/
